export function castIconType(value) {
    switch (value) {
        case "ArrowBack":
        case "ArrowDropDown":
        case "ArrowDropUp":
        case "ArrowLeft":
        case "ArrowRight":
        case "Autorenew":
        case "CalendarToday":
        case "CheckBox":
        case "CheckBoxOutlineBlank":
            return value;
        default:
            return undefined;
    }
}
